import { toggleClassListOfElements } from '../util/common.js';
import { PURCHASED_LOTTO_TEMPLATE, getLottoListTemplate } from './template.js';

export default class PurchasedLottoView {
  constructor() {
    this.initDom();
  }

  initDom() {
    this.container = document.getElementById('purchased-lotto-container');
  }

  render(lottoCount, lottos) {
    this.container.insertAdjacentHTML('beforeend', PURCHASED_LOTTO_TEMPLATE);
    this.purchasedLottoCount = document.getElementById('purchased-lotto-count');
    this.purchasedLottoCount.textContent = lottoCount;

    this.renderPurchasedLottoList(lottos);
    this.addToggleClickEvent();
  }

  renderPurchasedLottoList(lottos) {
    const lottoList = getLottoListTemplate(lottos);
    this.purchasedLottoList = document.getElementById('purchased-lotto-list');
    this.purchasedLottoList.insertAdjacentHTML('afterbegin', lottoList);
  }

  addToggleClickEvent() {
    this.toggle = document.getElementById('on-off-switch');

    this.toggle.addEventListener('click', () => {
      const purchasedLottoNumbers = document.querySelectorAll(
        '.purchased-lotto-number',
      );
      toggleClassListOfElements(purchasedLottoNumbers, 'hidden');

      const purchasedLottoItems = document.querySelectorAll(
        '.purchased-lotto-item',
      );
      ['detail-off', 'detail-on'].forEach(className =>
        toggleClassListOfElements(purchasedLottoItems, className),
      );
    });
  }

  reset() {
    this.container.removeChild(this.container.lastElementChild);
  }
}
